
import { Box, Grid } from "@mui/material";

import App from "components";

import EXPERIENCE from "./EXPERIENCE";
import SUMMARY from "./SUMMARY";
import TECHNICAL from "./TECHNICAL";

const HomePage = () => {
  return (<Box sx={{ display: "flex", justifyContent: "center" }}>
    <Box sx={{ p: 2, maxWidth: 1024 }}>
      <Grid container item
        spacing={1} sx={{ alignItems: "center", display: "flex", position: "relative" }}>
        <Grid container item {... { xs: 12 }} sx={{ position: "sticky" }}>
          <Grid item {... { xs: 6, sm: 6, md: 6, lg: 6 }}>
            <App.Label color="#87efad" text={"Nirun Tulayote"}
              size={{ xs: 18, sm: 24 }} />
          </Grid>
          <Grid item {... {
            xs: 6, sm: 6, md: 6, lg: 6,
            alignItems: "center", display: "flex",
            justifyContent: "right"
          }}>
            <img loading="lazy" src="https://raw.githubusercontent.com/Tarikul-Islam-Anik/Animated-Fluent-Emojis/master/Emojis/Animals/Bat.png" alt="Bat" width="25" height="25" />
          </Grid>
        </Grid>
        <Grid container item {... { xs: 12 }} sx={{ mb: 5 }}>
          <Grid item {... { xs: 12 }} sx={{ display: "flex", justifyContent: { xs: "center", sm: "end" } }}>
            <Box sx={{ width: 150 }}>
              <img loading="lazy"
                src="/image/programer.gif"
                style={{ maxWidth: "100%" }}
                data-target="animated-image.originalImage"
                alt="icon">
              </img>
            </Box>
          </Grid>
          <Grid item {... { xs: 12 }} sx={{
            display: "flex", justifyContent: "center",
            alignItems: "center"
          }}>
            <Box>
              <Box sx={{ display: "flex", justifyContent: "center" }}>
                <App.Label color="#87efad" text={"Hello.. I'am Nirun Tulayote"}
                  size={{ xs: 24, sm: 36 }} />
              </Box>
              <Box sx={{ display: "flex", justifyContent: "center" }}>
                <App.Label color="#87efad" text={"Senior Full Stack Developer"}
                  size={{ xs: 18, sm: 24 }} />
              </Box>
            </Box>

          </Grid>



        </Grid>

        <SUMMARY />
        <TECHNICAL />

        <EXPERIENCE />
      </Grid>


    </Box >
  </Box >
  )
};

export default HomePage;
